    saveProject(state) {//    
        var FileSaver = require('file-saver');
        var saveProject = []
        saveProject.push({  sahl: 'BNSoft',
                            project:state.SAHLProject[state.openProjectIndex], 
                            navigator: state.navigatorList[state.openProjectIndex], 
                            Line: state.connectionLine[state.openProjectIndex],
                            openElement: state.openElement,
                            activeLine: state.connectionsLine})
        var blob = new Blob([JSON.stringify(saveProject)], { type: "text/plain;charset=utf-8" });
        try { FileSaver.saveAs(blob, state.SAHLProject[state.openProjectIndex].name+'_SAHL'); } catch (e) { alert('Failed to save the Project !'); }
    },
    loadProject(state, payload) {
        this.commit('deleteProject', {})
        this.commit('setmakeProject', {makeproject:true})
        payload.project.forEach( item => {
            state.SAHLProject.push(item.project)
            state.navigatorList.push(item.navigator)
            state.connectionLine.push(item.Line)
            state.openElement = JSON.parse(JSON.stringify(item.openElement))
            state.connectionsLine = JSON.parse(JSON.stringify(item.activeLine))
        })
        console.log(state.connectionsLine)
        var setUUID = null
        if (state.openElement.length > 0) {
            setUUID = state.openElement[0].uuid
            this.commit('setuuid', {uuid: state.openElement[0].uuid})
        }
        this.commit('setEditInputElementList',{})
        this.commit('selectOpenProject', {openProjectIndex: state.SAHLProject.length - 1})
        EventBus.$emit('openAll-List', setUUID)
        Vue.nextTick(() => {
            if (state.connectionsLine.length > 0) {
                state.connectionsLine.forEach(item => {
                    var startUUID = item.start.split('/')
                    var sameUUID = 0
                    var isLineStart = false
                    EventBus.$emit(startUUID[0], item.refNum, item.idxID, item.tabID, item.twoTab) //twoTab ethernetC .
                    state.connectionsLine.forEach(data => { //      element closd  
                        var startuuid = data.start.split('/')
                        if (startuuid[0] == startUUID[0]) {
                            sameUUID++
                        }
                        if (startuuid[0] == item.end) {
                            isLineStart = true
                        }
                    })
                    console.log('&& '+ sameUUID + ' / ' + isLineStart)
                    if (sameUUID > 1 && !isLineStart) {
                        EventBus.$emit('Element-open', false, item.end)
                    }
                    if (item.end != null) {
                        setTimeout(() => {
                            EventBus.$emit('new-line', item.start, item.end, item.iscircle)
                            this.commit('setLoadingImport', {setProgress: false})
                        }, 300);
                    }
                })
            } else {
                this.commit('setLoadingImport', {setProgress: false})
            }
        })
       /* Vue.nextTick(() => {
            state.connectionsLine.forEach(item => {
                //var startUUID = item.start.split('/')
                //EventBus.$emit(startUUID[0], item.refNum, item.idxID, item.tabID)
                //Vue.nextTick(() => {
                    if (item.end != null) {
                        setTimeout(() => {EventBus.$emit('new-line', item.start, item.end, item.iscircle)}, 300);
                    }
                //})
            })
        })*/
    },
