        state.connectionsLine.push({start:payload.start, end:payload.end, iscircle:payload.iscircle, refNum: payload.refNum, idxID:payload.idxID, tabID: payload.tabID, twoTab: payload.twoTab})
        console.log(state.connectionsLine)
